.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7); 
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: #ffffff;
  padding: 20px;
  border-radius: 10px; 
  width: 30%;
  max-width: 90%;
  text-align: left;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
  animation: fadeIn 0.3s ease;
  background-color: #ecf0f1;
}

.modal-content h2 {
  margin: 0;
  font-size: 22px;
  color: #2c3e50; 
  text-align: center;
  font-weight: 600;
  margin-bottom: 15px;
}

.modal-content label {
  display: block;
  margin-top: 20px;
  font-weight: 600;
  color: #34495e; 
  font-size: 15px;
}

.modal-content input[type="text"] {
  width: 100%;
  padding: 10px;
  margin-top: 5px;
  font-size: 15px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #ffffff;
  color: #34495e;
  box-sizing: border-box;
}

.modal-content .modal-actions {
  display: flex;
  justify-content: flex-start; 
  gap: 10px; 
  margin-top: 15px;
}

button {
  padding: 10px 20px;
  font-size: 14px;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  border: none;
}

button.save-btn {
  background-color: #f5b041;
  color: white;
}

button.save-btn:hover {
  background-color: #f39c12;
}

button.close-btn {
  background-color: #e74c3c;
  color: white;
  border-radius: 6px;
  cursor: pointer;
}

button.close-btn:hover {
  background-color: #c0392b;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

