  z-index: 1000;
}

.spinner {
  width: 20px;
  height: 20px;
  background-color: #fff;
  border-radius: 50%;
  animation: zoom 1.5s ease-in-out infinite;
}

@keyframes zoom {
  0% {
    transform: scale(0.8);
    opacity: 0.8;
  }
  50% {
    transform: scale(1.5);
    opacity: 1;
  }
  100% {
    transform: scale(0.8);
    opacity: 0.8;
  }
}


